<template>
  <div style="margin: 0; padding: 0">
    <el-dropdown size="mini">
      <slot></slot>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="getPersonInfo">
          个人资料
        </el-dropdown-item>
        <el-dropdown-item @click.native="$refs.avatar.click()">
          上传头像
        </el-dropdown-item>
        <hr>
        <el-dropdown-item @click.native="quit">
          退出
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog title="个人资料" :visible.sync="dialogTableVisible">
      <el-descriptions title="用户信息">
        <el-descriptions-item :label="key" v-for="(key, index) in Object.keys(personList)" :key="index">{{personList[key]}}</el-descriptions-item>
      </el-descriptions>
    </el-dialog>
    <input type="file" accept="image/x-png,image/gif,image/jpeg,image/bmp" style="display: none" ref="avatar" @change="uploadAvatar($event)"/>
  </div>

</template>

<script>
import Cookies from "js-cookie";
import axios from "axios";
import qs from "qs";
export default {
  name: "DropDown",
  data() {
    return {
      dialogTableVisible: false,
      personList: []
    }
  },
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose
      }).then(action => {
        this.open('退出成功', 'success')
        this.$router.replace('/login')
      })
    },
    quit() {
      const h = this.$createElement;
      let message = h('p', null, '是否退出？')
      let beforeClose = (action, instance, done) => {
        if (action === 'confirm') {
          Cookies.remove('id')
          Cookies.remove('detail')
          instance.confirmButtonLoading = true
          instance.confirmButtonText = '退出中...';
          done()
          instance.confirmButtonLoading = false
        } else {
          done();
        }
      }
      this.checkBox('验证信息', message, true, beforeClose)
    },
    getPersonInfo() {
      //http://localhost:8080/api
      axios.post('/user/get_personal_info', qs.stringify({
        'no': JSON.parse(Cookies.get('detail'))['no']}, {indices: false})).then(
            res => {
              this.personList = res.data
              this.dialogTableVisible = true
              console.log(res.data)
            }
      )
    },
    uploadAvatar(e) {
      let file = e.target.files[0];
      let param = new FormData(); //创建form对象
      param.append('img',file);//通过append向form对象添加数据
      param.append('no', JSON.parse(Cookies.get('detail'))['no'])
      console.log(param.get('video')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
      let config = {
        headers:{'Content-Type':'multipart/form-data'}
      }
      //http://localhost:8080/api
      axios.post("/user/upload_avatar", param, config).then(
          res => {
            if (res.data.status === 'ok')
              this.$message.success('上传成功')
            else
              this.$message.error('上传失败')
          }
      )
    }
  }
}
</script>

