<template>
  <div class="upload">
<!--    http://localhost:8080/api/video/upload_files-->
    <el-upload
        class="upload-demo"
        action="/video/upload_files"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        :limit="3"
        :on-exceed="handleExceed"
        :auto-upload="false"
        :file-list="fileList"
        ref="upload"
        :data="account"
        :on-success="handleSuccess"
        multiple
    >
      <el-button size="small" type="primary" slot="trigger">点击上传</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
      <div slot="tip" class="el-upload__tip">文件数不能超过3个</div>
    </el-upload>
  </div>

</template>

<script>
import Cookies from "js-cookie";
export default {
  data() {
    return {
      fileList: [],
      account: {
        account: JSON.parse(Cookies.get('detail'))['no'],
        location: ''
      }
    }
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    submitUpload() {
      this.$prompt('请输入视频录制地点', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValidator: function (value) {
          if (!value)
            return '输入不能为空'
        },
        // inputErrorMessage: '邮箱格式不正确'
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: '录制地点: ' + value
        })
        this.account.location = value
        this.$refs.upload.submit()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        });
      });
    },
    handleSuccess(response, file, fileList) {
      console.log(response.data)
      this.$message.success(`${file.name}上传完成`)
      this.$bus.$emit('refreshComponent')
    }
  },
  mounted() {

  }
}
</script>

<style>
.upload {
  width: 300px;
  justify-content: center;
}
</style>